import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import Grid from '@material-ui/core/Grid';
import InputAdornment from '@material-ui/core/InputAdornment';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import PropTypes from 'prop-types';
import styles from './ButtonSwitchStyle';

const {inspect} = require('util');


const IOSSwitch = withStyles(theme => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: '#47bdd7',
      '& + $track': {
        backgroundColor: 'white',

      },
    },
    '&$focusVisible $thumb': {
      color: 'white',
      border: '6px solid #fff',
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border']),
  },
  checked: {},
  focusVisible: {},
}))(({classes, ...props}) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});

const CssTextField = withStyles({
  root: {
    '& label': {
      fontSize: '0.8rem',
    },
  },
})(TextField);


class ButtonSwitch extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      checked: this.props.checked,
      billing: props.billing ? props.billing._id : props.isOption ? this.props.billings[0]._id : null,
      price: this.props.price,
      label: this.props.label,
    };

    this.onToggle = this.onToggle.bind(this);
    this.onChangeBilling = this.onChangeBilling.bind(this);
    this.onChangePrice = this.onChangePrice.bind(this);
    this.onChangeLabel = this.onChangeLabel.bind(this);

    this.fireChange = this.fireChange.bind(this);
  }

  fireChange(id, checked, price, billing, label) {
    if (this.props.onChange) {
      this.props.onChange(this.props.id, this.state.checked, this.state.checked ? this.state.price : null, this.state.billing, this.state.label);
    }
  }

  onToggle(value) {
    this.setState({checked: !this.state.checked}, () => this.fireChange());
  };

  onChangeBilling(event, index) {
    this.setState({billing: event.target.value}, () => this.fireChange());
  }

  onChangePrice(event) {
    var price = parseInt(event.target.value);
    if (isNaN(price)) {
      price = null;
    }
    this.setState({price: price}, () => this.fireChange());
  }

  onChangeLabel(event) {
    this.setState({label: event.target.value}, () => this.fireChange());
  }

  render() {
    const {classes, isEditable, isOption, isPrice, billings, priceDisabled} = this.props;
    var {label, checked} = this.state;

    return (
      <Grid className={classes.contentFiltre}>
        <Grid className={classes.responsiveIOSswitch} style={{width: this.props.width}}>
          <Grid>
            <IOSSwitch
              color="primary"
              type="checkbox"
              checked={checked}
              onChange={this.onToggle}
            />
          </Grid>
          <Grid>
            <span>
        {isEditable ?
          <CssTextField
            label={'Intitulé'}
            placeholder={'Saisissez un intitulé'}
            value={this.state.label}
            onChange={this.onChangeLabel}
            error={!this.state.label}
            helperText={this.state.label ? null : 'Obligatoire'}
          />
          :
          label === undefined ? 'label introuvable' : label}
            </span>
          </Grid>
        </Grid>
        {isPrice ?
          <Grid className={classes.responsiveIOSswitchContent}>
            {checked === true ?
              <Grid style={{display: 'flex'}}>
                <CssTextField
                  value={this.state.price}
                  label={'Tarif'}
                  type="number"
                  className={classes.textField}
                  disabled={!checked || priceDisabled}
                  onChange={this.onChangePrice}
                  InputProps={{
                    inputProps: {
                      min: 0,
                    },
                    endAdornment: <InputAdornment position="start">€</InputAdornment>,
                  }}
                  error={!this.state.price}
                  helperText={this.state.price ? null : 'Obligatoire'}
                />
                {isOption ?
                  <Select
                    style={{
                      width: '100px',
                      fontSize: '0.8rem',
                    }}
                    disabled={!checked}
                    margin="none"
                    onChange={this.onChangeBilling}
                    value={this.state.billing}
                  >
                    {billings.map(bill => {
                        return (
                          <MenuItem value={bill._id.toString()}>{bill.label}</MenuItem>
                        );
                      },
                    )
                    }
                  </Select> : null
                }
              </Grid>
              : null
            }
          </Grid> : null
        }

      </Grid>
    );
  }
}

export default withStyles(styles)(ButtonSwitch);
